// *************************************************
// ********************* MAP ***********************
// *************************************************

@use "colors" as *;
@use "zmdi" as *;
@use "variables" as *;

$height-photon: 46px;

.nohead {
  @media (max-width: $smartphone) {
    top: 0; // !important;
  }
}

.geo {
  float: right;
  visibility: hidden;
  width: 0;

  @media (max-width: $smartphone) {
    display: block;
    float: none;
    visibility: visible;
  }

  font-family: $zmdi-font-name;
}

.map {
  .leaflet-container {
    inset: 60px 0 0;
    position: absolute;
    width: 100%;

    .photon-input {
      margin-bottom: 0;
      width: 400px;

      @media (max-width: $smartphone) {
        margin-top: -10px;
        width: 100%;
      }
    }

    .leaflet-popup-content {
      text-align: center;
    }

    .leaflet-control-geoloc {
      background: $color-leaflet-background;
      border-radius: 5px;
      box-shadow: 0 1px 5px rgb(0 0 0 / 40%);
      color: $color-leaflet-button;
      padding: 6px;
      visibility: hidden;

      @media (max-width: $smartphone) {
        position: absolute;
        right: 0;
        top: 90px;
        visibility: visible;
      }
    }

    .leaflet-right {
      width: 100%;
    }

    .leaflet-top {
      @media print {
        visibility: hidden;
      }

      .leaflet-right {
        @media (max-width: $smartphone) {
          display: block;
          top: 45px;
        }

        @media print {
          visibility: hidden;
        }
      }
    }

    .leaflet-control-layers {
      position: absolute;
      right: 0;

      // top: 0;

      @media (max-width: $smartphone) {
        display: block;
        top: $height-photon; // hack
      }

      @media print {
        visibility: hidden;
      }
    }

    .leaflet-control-layers-toggle::before {
      content: $zmdi-var-layers;
    }

    .leaflet-control-layers-toggle {
      // background-image: url(../images/layers.png);
      // width: 36px;
      // height: 36px;
      background-image: none;
      color: $color-leaflet-button;
      font: normal normal normal 28px/1 $zmdi-font-name;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      height: 100%;
      padding: 5px;

      // position: absolute;
      text-decoration: none;
      text-rendering: auto;
      width: 100%;
    }

    .leaflet-photon {
      right: calc(50% - 200px);

      @media (max-width: $smartphone) {
        margin: 0;
        right: 0;
        top: 0;
        width: 100%;
      }
    }

    .leaflet-control-permalink {
      @media print {
        display: none;
      }
    }

    @media (max-width: $smartphone) {
      top: 60px;
    }

    @media print {
      height: 100%;
      top: 0;
    }
  }

  .leaflet-bar {
    a {
      color: $color-leaflet-button;
    }
  }

  footer {
    display: none;
  }
}

.reverse-label {
  background-color: $color-near-white;
  border: 1px solid $color-border;
  bottom: 0;
  color: $color-dark-text;
  margin-bottom: 0; // !important;
  opacity: 0.8;
  padding: 0 10px;
  position: fixed;
  right: 0;
  text-align: center;

  @media (max-width: $smartphone) {
    bottom: 2.5em;
    margin-right: 0; // !important;
    width: 100%;
  }

  @media print {
    bottom: inherit; // need???
    font-size: x-large;
    height: 1.5em;
    top: 0;
    visibility: visible; // need??
  }
}

.leaflet-top.leaflet-left {
  @media (max-width: $smartphone) {
    display: block;
    top: $height-photon; // hack
  }

  @media print {
    visibility: hidden;
  }
}

.leaflet-bottom .leaflet-control {
  margin: 0;
}

.photon-autocomplete li .type {
  float: right;
  font-family: "open_sanslight", sans-serif;
  font-size: 0.8em;
  font-weight: normal;
}
